<script>
import ModelAddress from "../components/model-address/src/main";
import ModelSex from "../components/model-sex/src/main";
import countDown from "@/mixins/count-down";
import {req,setCookie} from "../https/request"
import { Toast } from "vant";
import test from "../libs/test.js";
import Loading from "../components/Loading.vue";
import { log } from 'util';
// import axios from "axios"

export default {
  components: { ModelSex, ModelAddress, Loading },
  mixins: [countDown],
  data: function() {
    return {
      publicPath: process.env.BASE_URL,
      address_show: false,
      address_data: [],
      sex_show: false,
      sex_data: {},
      code: {
        buttonText: "",
        buttonStatus: false
      },
      inputName: "",
      inputPhone: "",
      inputSms: "",
      guid: "",
      pasWordMD5: "",
      // hinText : '',
      send_url:
        "https://demo.clarabeautynism.com/Mercury/TraditionalMedicalConstitution/FastRegistration"
    };
  },
  methods: {
    start: function() {
      let testData = [
        {
          value: this.inputName,
          rules: "",
          key: "userName"
        },
        {
          value: this.sex_data.value || "",
          rules: "",
          key: "userGender"
        },
        {
          value: this.address_data.join("-"),
          rules: "",
          key: "city"
        },
        {
          value: this.inputPhone,
          rules: {
            empty: "",
            mobile: ""
          },
          key: "phoneNumber"
        },
        {
          value: this.inputSms,
          rules: "",
          key: "phoneCode"
        }
      ];

      testData = test.testArray(testData);

      if (!testData.test) {
        return this.toast(testData.msg);
      }

      let obj = testData.data;

      obj["phoneInfo"] = this.count_down_info;
      let city = obj["city"].split("-");
      obj["province"] = city[0];
      obj["city"] = city[1];

      let formData = new FormData();
      for (let key in obj) {
        formData.append(key, obj[key]);
      }
      req
        .post(this.send_url, formData)
        .then(res => {
          console.log("",res);
          
          if (res.data) {
            console.log("ticket", res.data.ticketID);
            req
              .get(
                "https://demo.clarabeautynism.com/Mercury/Questionnaire/StartByTicket?ticketID=" +
                  res.data.ticketID
              )
              .then(res => {
                console.log("ticket", res);
                let params = {
                  guid: res.data.guid,
                };
                console.log("token",res.data.authorization);
                
                setCookie("token",res.data.authorization)
                // -axiostoken
                this.$router.push({path:"/cardanswer", query:params});
              });
          }
        })
        .catch(() => {
          this.toast.fail("");
        });
    },

    confirmAddress: function(data) {
      this.address_data = data.value;
    },
    sexChange: function(item) {
      console.log(item);
      this.sex_data = item;
    }
  },
  created() {
    Toast.setDefaultOptions({
      className: "toast-box"
    });
    this.toast = Toast;
  }
};
</script>

