const baseSize = 32;
// 
window.radio = 0;
//  rem 
function setRem () {
  //  750 
  const scale = document.documentElement.clientWidth / 750;
  window.radio = scale;
  // 
  document.documentElement.style.fontSize = (baseSize * Math.min(scale, 2)) + 'px'
}
// 
setRem()
//  rem
window.onresize = function () {
  setRem()
}
/*
  
  @params value Number  px 
  @params unit Boolean | String    (true)  rem        ---> false     Number 
  @import  window.radio Number  rem -> px 
*/
window.unit = function(value, unit=false){
  value = parseFloat(value);
  if(value !== value || typeof value !== 'number') return 0;
  return ((value * radio) / (baseSize * radio) ) +  (unit ? typeof unit == 'boolean' ? 'rem' : unit : 0);
};


//  px   
window.unitParse = function (value) {

  value = parseFloat(value);

  return value * radio;

};
