        //=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=
        /**
         * 
         *  
         */
        _distributeCardsClass(data){
            let className = data.type;
            switch(className){
                case this.typeBooleanOption:
                    //
                    console.log('class=',' ');
                    //ui
                    this.showBooleanTypeDiv = true,
                    this.showClassDiv = this.typeBooleanOption;
                    this.showBg = true;
                    //guid
                    this.guid = data.guid;
                    //
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextLeft = data.options.right.text;
                    this.uiTextRight = data.options.left.text;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;
                    //
                    // this.fanpai();
                    this.fanpaiBool();
                    break;
                case this.typeSingleOption:
                    //
                    console.log('class=',' ');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typeSingleOption;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    let list = [];
                    data.options.map((item,index) => {
                        let param = {
                            is : index == 0 ? true : false,
                            title : item,
                        }
                        list.push(param);
                    })
                    this.horList =list;
                    console.log(this.cardOption.SingleOption);
                    //  x 
                    this.horMoveX = this.cardOption.SingleOption.avg;
                    this.horMoveEndX = this.horMoveX;
                    this.uiTextIndex += 1;
                    this.titleNumber=data.progress;

                    //
                    this.singleFanka();
                    break;
                case this.typeMultiOption:
                    //
                    console.log('class=','');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typeMultiOption;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    let listm = [];
                    data.options.map((item,index) => {
                        let param = {
                            is : false,
                            title : item,
                        }
                        listm.push(param);
                    })
                    this.multiList =listm;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;

                    //
                    this.singleFanka();
                    break;
                case this.typeScoring:
                    //
                    console.log('class=','');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typeScoring;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;

                    //
                    this.singleFanka();
                    break;
                case this.typeTextBox:
                    //
                    console.log('class=','');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typeTextBox;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextIndex += 1;
                    this.titleNumber += 1;

                    //
                    this.singleFanka();
                    break;
                case this.typePhotoGraph:
                    //
                    console.log('class=','');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typePhotoGraph;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;

                    //
                    this.singleFanka();
                    break;
                case this.typeUploadFiles:
                    //
                    console.log('class=','');
                    this.showBooleanTypeDiv = false,
                    this.showClassDiv = this.typeUploadFiles;
                    //guid
                    this.guid = data.guid;
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;

                    //
                    this.singleFanka();
                    break;
                case this.typeToastNotice:
                    //guid
                    this.guid = data.guid;
                    this.toastText = data.newCardDescription;
                    //
                    console.log('class=','');
                    this.newToastShow = true;
                    this.kacIs = true, //
                    this.showToastDiv = false, //    
                    this.showCardsBackOrCardsFront = false, // false   true

                    this.newCardsToast();
                    //
                    this.toastFanpai();
                    break;
                case this.typeConclusion:
                    //
                    console.log('class=','Conclusion ');
                    //ui
                    this.showBooleanTypeDiv = true,
                    this.showClassDiv = this.typeBooleanOption;
                    this.showBg = true;
                    //guid
                    this.guid = data.guid;
                    //
                    this.uiTextClassName = '';
                    // this.uiTextContent = data.content;
                    this.uiTextContent = data.title;
                    this.uiTextLeft = data.options.Right.text;
                    this.uiTextRight = data.options.Left.text;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;

                    //
                    this.fanpaiBool();
                    break;
                case this.typeBreakConclusion:
                    //
                    console.log('class=','break ',data);
                    //ui
                    this.showBooleanTypeDiv = true,
                    this.showClassDiv = this.typeBooleanOption;
                    this.showBg = false;
                    //guid
                    this.guid = data.guid;
                    //
                    this.uiTextClassName = '';
                    this.uiTextContent = data.content;
                    this.uiTextLeft = data.options.Left.text;
                    this.uiTextRight = data.options.Right.text;
                    this.uiTextIndex += 1;
                    this.titleNumber =data.progress;
                    this.link=data.link

                    this.isOver = true;
                    //
                    this.fanpaiBool();
                    break;
            }
        },
