                                })
                        }
                    }
                }
            )

            if(this.isOver){

            }else{
                //
                Velocity(this.$refs.kapai,
                    { transform: [ 'translateX(0) translateY(0) rotate(0)','translateX(0.1px) translateY(0.1px) rotate(0.1deg)' ] },
