import axios from "axios"
import router from "../router"
import { Toast } from "vant"
// axios
var req = axios.create({ timeout: 1000 * 10 })
// post()
req.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';

// token
req.interceptors.request.use(
    config => {
        const token = getCookie("token")
        token && (config.headers.Authorization = 'Bearer ' + token)
        return config
    }
)
// 
req.interceptors.response.use(
    res => {
        if (res.status === 200) {
            return Promise.resolve(res)
        } else {
            return Promise.reject(res)
        }
    },
    error => {
        const { response } = error
        if (response) {
            errorHandle(response.status, response.data.message)
            return Promise.reject(response)
        } else {
            tip("")
        }
    })

// cookie
function getCookie(name) {
    var arr = document.cookie.match(new RegExp("(^| )" + name + "=([^;]*)(;|$)"));
    if (arr != null) return decodeURIComponent(arr[2]);
    return null;
}
// cookie
function removeCookie(name) {
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval = getCookie(name);
    if (!cval) document.cookie = name + "=" + cval + ";expires=" + exp.toUTCString();
}

// cookie

function setCookie(name, value) {
    if (!name || !value) return;
    var Days = 30;//30
    var exp = new Date();
    exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
    document.cookie = name + "=" + encodeURIComponent(value) + ";expires=" + exp.toUTCString();
}

// 
const errorHandle = (statusCode, other) => {
    if (statusCode) {
        console.log("", statusCode);
        switch (error.response.status) {
            // 401 ->
            case 401: router.replace({
                path: "/",
                query: {
                    redirect: router.currentRoute.fullPath
                }
            });
                break;
            case 403:
                Toast({
                    message: '',
                    duration: 1000,
                    forbidClick: true
                });
                removeCookie("token")
                // 
                setTimeout(() => {
                    toLogin()
                }, 1000);
                break;
            case 404:
                tip("")
                break;
            default:
                console.log(other);
        }
    }
}

// 
const tip = msg => {
    console.log("");

    Toast({
        message: msg,
        duration: 2000,
        forbidClick: true
    });
}

// 
const toLogin = () => {
    console.log("");
    router.replace({
        path: '/',
        query: {
            redirect: router.currentRoute.fullPath
        }
    })
}

export { req, setCookie, removeCookie, getCookie }
